import SfButton from "../../atoms/SfButton/SfButton.vue";
export default {
  name: "SfCallToAction",
  components: {
    SfButton,
  },
  props: {
    /**
     * CallToAction title.
     */
    title: {
      type: String,
      default: "",
    },
    /**
     * Text that will be displayed inside the button.
     */
    buttonText: {
      type: String,
      default: "",
    },
    /**
     *  CallToAction link. If it's filled in, changes button tag on a tag.
     */
    link: {
      type: String,
      default: "",
    },
    /**
     * CallToAction description.
     */
    description: {
      type: String,
      default: "",
    },
    /**
     * Background color.
     */
    background: {
      type: String,
      default: "",
    },
    /**
     * Background image.
     */
    image: {
      type: [String, Object],
      default: "",
    },
  },
  computed: {
    style() {
      const image = this.image;
      const background = this.background;
      return {
        "--_call-to-action-background-image": image.mobile
          ? `url(${image.mobile})`
          : `url(${image})`,
        "--_call-to-action-background-desktop-image":
          image.desktop && `url(${image.desktop})`,
        "--_call-to-action-background-color": background,
      };
    },
  },
};
